<template>
    <div class="shop" v-if="showlist[id-1]"> 
        <div class="header">
            <span class="iconfont back" @click="back">&#xe8b5;</span>
            <span>商品详情</span>
        </div>
        <div class="img">
            <img :src="showlist[id-1].imgUrl" alt="">
        </div>
        <div class="center">
            <div class="center-price">
                <span class="center-price-icon">￥</span>
                <span class="center-price-num">{{showlist[id-1].tages[1]}}</span>
            </div>
            <div class="center-sum">{{showlist[id-1].tages[2]}}</div>
            <div class="center-detail">{{showlist[id-1].desc}}</div>
            <div class="center-icon">
                <span>
                    <span class="iconfont">&#xe6d4;</span>
                    <span class="text">推荐</span>
                </span>
                <span>
                    <span class="iconfont">&#xe610;</span>
                    <span class="text">帮我选</span>
                </span>
                <span>
                    <span class="iconfont">&#xe63c;</span>
                    <span class="text">分享</span>
                </span>
            </div>
        </div>
        <div class="footer">
            <div class="footer-icon">
                <span class="footer-icon-list">
                    <span class="iconfont">&#xe600;</span>
                    <span class="text">店铺</span>
                </span>
                <span class="footer-icon-list">
                    <span class="iconfont">&#xec2e;</span>
                    <span class="text">客服</span>
                </span>
                <span class="footer-icon-list">
                    <span class="iconfont">&#xe602;</span>
                    <span class="text">收藏</span>
                </span>
            </div>
            <div class="footer-button">
                <span class="add" @click="() =>{ addCartItemInfo(id, showlist[id-1]) }">加入购物车</span>
                <span class="buy">立即购买</span>
            </div>
        </div>
    </div>
</template>     

<script>
import {useRouter,useRoute} from 'vue-router'
import {get} from '../../untils/request'
import { reactive ,toRefs } from 'vue'
import { useStore } from 'vuex'
// 获取商品信息
const  useShopEffect=()=>{
    // const shoplist=ref([]);
    const data = reactive({showlist:{}});
    const pleaseShop=async () =>{
        const result = await get('/api/show')
        if(result.code===0){
            data.showlist=result.data.showlist
        }
    }
    const {showlist} = toRefs(data);
    const store = useStore()
    const { cartlist } = toRefs(store.state);
    const addCartItemInfo=(productId, productInfo)=>{
        store.commit('addCartItemInfo', {
            productId, productInfo
        })
    }
    return {showlist,pleaseShop,
        cartlist,addCartItemInfo
    }
}


export default {
    setup() {
        const router= useRouter();
        const route =useRoute();
        const id=route.params.id;
        const {showlist,pleaseShop,cartlist,addCartItemInfo}= useShopEffect();
        
        pleaseShop()
        // console.log(shoplist);
        // console.log(shoplist.value);
        // console.log(showlist);
        const back = ()=>{
            router.push({name:'Home'})
        }
        return {back,showlist,id,cartlist,addCartItemInfo}
    },
}
</script>

<style scoped lang='scss'>
.shop{
    width: 100%;
    height: 100%;
    background: #F0F0F0;
    .header{
        width: 100%;
        height: 60px;
        line-height: 60px;
        background: #19C8AB;
        span{
            
            font-size: 16px;
            color: #fff;
        }
        .back{
            font-size: 20px;
            margin-left: 20px;
        }
    }
    .img{
        width: 100%;
        height: 45%;
        text-align: center;
        img{
            margin-top: 10px;
        }
    }
    .center{
        width: 90%;
        padding-bottom: 10px;
        margin: 10px auto;
        border-radius: 15px;
        background: #fff;
        &-price{
            padding: 10px 0 0 10px;
            color: #FD540A;
            &-icon{
                font-size: 16px;
            }
            &-num{
                font-size: 20px;
            }
        }
        &-sum{
            margin-right: 10px;
            text-align:right;
            color: #ccc;
        }
        &-detail{
            margin: 10px;
            font-weight: 800;
            font-size: 16px;
        }
        &-icon{
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;
            background: #ffffff;
        }
    }
    .footer{
        width: 100%;
        height: 55px;
        display: flex;
        margin-top:80px;
        justify-content: space-around;
        background: #ffffff;
       
        &-icon{
            flex-grow: 1;
            display: flex;
            justify-content: space-around;
            
            &-list{
                line-height: 55px;
                
            }
        }
        &-button{
            flex-grow: 2;
            display: flex;
            justify-content: space-around;
            font-size: 16px;
            color: #fff;
            height: 55px;
            line-height:55px;    
            text-align: center;
            .add{
                flex-grow: 1;
                background: #FEA400;
            }
            .buy{
                flex-grow: 1;
                background: #EB444E;
            }
        }
    }
}


</style>